<template>
  <d2-container>
    <template slot="header">
      <el-select
        v-model="serviceName"
        placeholder="请选择服务"
        size="small"
        @change="changeService"
        filterable
        clearable
      >
        <el-option
          v-for="item in serviceOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>
      <el-select
        v-model="instanceValue"
        placeholder="请选择您的实例"
        size="small"
        @change="changeInstance"
        filterable
        clearable
        style="width: 200px"
      >
        <el-option
          v-for="(item, index) in instanceOptions"
          :key="index"
          :label="item.instanceId.split('#')[0]"
          :value="item.instanceId"
        >
        </el-option>
      </el-select>

      <div class="topTime" style="float: right">
        <strong>时间：</strong>
        <span style="padding: 3px 0">{{ serverData.crt_time }}</span>
      </div>
    </template>

    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="平均QoS流数" name="平均QoS流数">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <strong>平均QoS流数： </strong>
            <span style="padding: 3px 0">{{
              serverData.upf_mean_qos_flows
            }}</span>
          </div>
          <el-row :gutter="12">
            <!-- <strong>分切片的平均QoS流数</strong> -->
            <el-col :lg="12" :md="12" :sm="24">
              <strong>分切片的平均QoS流数</strong>
              <el-table
                :data="serverData.meanQosFlowsNsList"
                style="width: 90%"
                border
              >
                <el-table-column prop="id" label="ID" width="180">
                </el-table-column>
                <el-table-column prop="ns_name" label="ns_name" width="180">
                </el-table-column>
                <el-table-column prop="mean_qos_flows" label="次数">
                </el-table-column>
              </el-table>
            </el-col>
            <!-- <strong>分DNN的平均QoS流数</strong> -->
            <el-col :lg="12" :md="12" :sm="24">
              <strong>分DNN的平均QoS流数</strong>
              <el-table
                :data="serverData.meanQosFlowsDnnList"
                style="width: 90%"
                border
              >
                <el-table-column prop="id" label="ID" width="180">
                </el-table-column>
                <el-table-column prop="ns_name" label="ns_name" width="180">
                </el-table-column>
                <el-table-column prop="mean_qos_flows" label="次数">
                </el-table-column>
              </el-table>
            </el-col>
          </el-row>
        </el-card>
      </el-tab-pane>
      <el-tab-pane label="最大QoS流数" name="最大QoS流数">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <strong>最大QoS流数： </strong>
            <span style="padding: 3px 0">{{
              serverData.upf_max_qos_flows
            }}</span>
          </div>
          <el-row :gutter="12">
            <!-- <strong>分切片的最大QoS流数</strong> -->
            <el-col :lg="12" :md="12" :sm="24">
              <strong>分切片的最大QoS流数</strong>
              <el-table
                :data="serverData.maxQosFlowsNsList"
                style="width: 90%"
                border
              >
                <el-table-column prop="id" label="ID" width="180">
                </el-table-column>
                <el-table-column prop="ns_name" label="ns_name" width="180">
                </el-table-column>
                <el-table-column prop="max_qos_flows" label="次数">
                </el-table-column>
              </el-table>
            </el-col>
            <!-- <strong>分DNN的最大QoS流数</strong> -->
            <el-col :lg="12" :md="12" :sm="24">
              <strong>分DNN的最大QoS流数</strong>
              <el-table
                :data="serverData.maxQosFlowsDnnList"
                style="width: 90%"
                border
              >
                <el-table-column prop="id" label="ID" width="180">
                </el-table-column>
                <el-table-column prop="ns_name" label="ns_name" width="180">
                </el-table-column>
                <el-table-column prop="max_qos_flows" label="次数">
                </el-table-column>
              </el-table>
            </el-col>
          </el-row>
        </el-card>
      </el-tab-pane>
    </el-tabs>
  </d2-container>
</template>

<script>
import {
  fetchGetServiceList,
  fetchGetInstanceList,
  fetchGetQosFlow,
} from "@/api/demo/index";
export default {
  name: "qosFlow",
  data() {
    return {
      activeName: "平均QoS流数",
      serviceName: "",
      serviceOptions: [],
      instanceOptions: [], //upf下面的实例
      instanceValue: "", //当前选中的实例
      isFirst: true,
      loading: false,
      serverData: [],
    };
  },
  mounted() {
    this.getService();
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 获取服务列表
    getService() {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          this.serviceOptions = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
          if (this.serviceOptions.length > 0) {
            this.serviceName = this.serviceOptions[0];
            this.getServiceInstance(this.serviceName);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 选择服务
    changeService(val) {
      if (!val) {
        return;
      }
      this.getServiceInstance(val);
    },
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.serverData = [];

      // 获取告警数据
      this.getData();
    },
    //获取服务下面的实例
    getServiceInstance(serviceName) {
      fetchGetInstanceList({ serviceName: serviceName })
        .then((res) => {
          this.instanceOptions = res.hosts;
          if (this.instanceOptions.length > 0) {
            this.instanceValue = this.instanceOptions[0].instanceId;
            this.changeInstance(this.instanceValue);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 获取数据
    getData() {
      this.loading = true;
      fetchGetQosFlow({
        request_type: "qos perform",
        instance_id: this.instanceValue,
        sequence_no: 4001,
      })
        .then((response) => {
          this.loading = false;
          console.log(response);
          if ((response != null) & (response != [])) {
            this.serverData = response;
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
  },
};
</script>

